// Customizations go here
$primary: #ffd700;
$secondary: #d0d000;
$dark: #a0a000;
$danger: #afff00;

$theme-colors: (
	"primary": $primary,
	"secondary": $secondary,
	"danger": $danger,
);

.btn {
	height: 45px;
	line-height: 45px;
	font-size: 16px;
	font-size: 0.88889rem;
	font-weight: 600;
	border: 0;
	outline: 0;
	text-transform: uppercase;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-o-border-radius: 3px;
	-ms-border-radius: 3px;
	border-radius: 3px;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	-o-transition: all 0.3s;
	-ms-transition: all 0.3s;
	transition: all 0.3s;
	.btn-secondary {
		border: 1px solid $primary;
		color: $dark;
		background-color: #ffffff;
	}

	.btn-primary {
		background-color: #4b5320;
		color: #fff;
	}
}

$btn-border-radius-sm: 0rem;
$btn-border-radius: 0rem;

$input-border-radius-sm: 0rem;
$input-border-radius: 0rem;

$form-control-border-radius-sm: 0rem;
$form-control-border-radius: 0rem;

$custom-file-input-border-radius-sm: 0rem;
$custom-file-input-border-radius: 0rem;

$custom-file-label-border-radius-sm: 0rem;
$label-border-radius: 0rem;

@import "../node_modules/bootstrap/scss/bootstrap.scss";

